import React, {Component} from "react";
import {connect} from "react-redux";
import DynamicIcon from "../../../svgs/dynamic.svg";
import DesktopIcon from "../../../svgs/desktopNew.svg";
import BindIcon from "../../../svgs/bind.svg";
import controllerDecorate from "../../decorators/controller";

class DimensionsController extends Component {
  constructor(props){
    super(props);
    this.changeValue = this.changeValue.bind(this);
    this.changeBind = this.changeBind.bind(this);
    this.changeUnit = this.changeUnit.bind(this);
    let value = this.props.currentElement.getSettings(this.props.controlId);
    // console.log(value);
    if(value === null && this.props.default){
      value = this.props.default ;
    }
    value = value || '';
    this.state = {
      value,
      fill: "#FFF", 
      active: true,
      // active: this.state.value.active,
      units: this.props.units || ['px'],
    };
    controllerDecorate(this);
  }

  changeUnit(e){
    let unit = e.target.dataset.unit;
    this._changeValue({
      ...this.state.value,
      unit
    });
  };

  changeValue(e){

    if(this.state.active == true){
      if( e.target.value <= 9999){
        this._changeValue({
          ...this.state.value,
          left: e.target.value,
          bottom: e.target.value,
          top: e.target.value,
          right: e.target.value
        })
      }
    } else {
      console.log(e.currentTarget.dataset.active)
      let active = e.currentTarget.dataset.active;

      if(active == "top"){
        this._changeValue({
          top:e.target.value,
          right: this.state.value.right,
          bottom: this.state.value.bottom,
          left: this.state.value.left,
          unit: this.state.value.unit
        });
      }

      if(active == "right"){
        this._changeValue({
          right:e.target.value,
          bottom: this.state.value.bottom,
          left: this.state.value.left,
          top: this.state.value.top,
          unit: this.state.value.unit
        });
      }

      if(active == "bottom"){
        this._changeValue({
          bottom:e.target.value,
          right: this.state.value.right,
          left: this.state.value.left,
          top: this.state.value.top,
          unit: this.state.value.unit
        });
      }

      if(active == "left"){
        this._changeValue({
          left:e.target.value,
          right: this.state.value.right,
          bottom: this.state.value.bottom,
          top: this.state.value.top,
          unit: this.state.value.unit
        });
      }
    }
    // console.log(this.state.value)

    // this.setState({
    //   value:e.target.value
    // });
    // this.props.currentElement.setSettingValue(this.props.controlId, e.target.value);

  }
  
  changeBind(){
    let bind = document.getElementById("bind");

    bind.classList.toggle("control-field-bind-active");

    if(this.state.fill == "#FFF") {
      this.setState({
        fill: "#8E94AA",
        active: false
      })
    } else {
      this.setState({
        fill: "#FFF",
        active: true
      })
    };
  }

  getDefaultValue(){
    return {
      left: '',
      top: '',
      bottom: '',
      right: '',
      unit: 'px',
    };
  }
  render(){

    return <div className="controller-container controller-container_dimensions">
      <div className="control-dimensions-header">
        <div className="controller-dimensions__label">{this.props.label}</div>
        <DesktopIcon className="controller-container__label-svg" width="12"/>

        <div className="control-slider-type">
          {
            this.state.units.map(unit=>{
              let classes = 'control-slider-type-box';
              if(this.state.value.unit === unit){
                classes += ' control-slider-type-box_active';
              }
              return <div className={classes}
                          key={unit}>
                <button onClick={this.changeUnit}
                        data-unit={unit}
                        className="control-slider-type-label">{unit}</button>
              </div>
            })
          }
        </div>
      </div>
      <div className="control-group">
        <div className="control-dimensions-container">
          <input className="control-field control-field-dimensions control-field-top-l" onChange={this.changeValue} data-active="top" value={this.state.value.top} type="number"/>
          <label className="control-field-top-l-label control-field-dimensions-label">TOP</label>
        </div>
        <div className="control-dimensions-container">
          <input className="control-field control-field-dimensions control-field-top-r" onChange={this.changeValue} data-active="right" value={this.state.value.right} type="number"/>
          <label className="control-field-top-r-label control-field-dimensions-label">RIGHT</label>
        </div>
        <div className="control-dimensions-container">
          <input className="control-field control-field-dimensions control-field-bot-l" onChange={this.changeValue} data-active="bottom" value={this.state.value.bottom} type="number"/>
          <label className="control-field-bot-l-label control-field-dimensions-label">BOTTOM</label>
        </div>
        <div className="control-dimensions-container">
          <input className="control-field control-field-dimensions control-field-bot-r" onChange={this.changeValue} data-active="left" value={this.state.value.left} type="number"/>
          <label className="control-field-bot-r-label control-field-dimensions-label">LEFT</label>
        </div>
        <div id="bind" className="control-field control-field-bind control-field-bind-active" onClick={this.changeBind}>
          <BindIcon width="12" height="12" fill={this.state.fill}/>
        </div>
      </div>
    </div>
  }
}

function mapStateToProps(state) {
  return{
    currentElement:state.currentElement.currentElement,
  };
}
export default connect(mapStateToProps)(DimensionsController);
